const firebaseConfig = {
    apiKey: 'AIzaSyB2FKP0oaAeLPa96h_SI7fFi4KEEaWrvxI',
    authDomain: "wordsmith-auth.firebaseapp.com",
    projectId: "wordsmith-auth",
    storageBucket: "wordsmith-auth.appspot.com",
    messagingSenderId: "315192723360",
    appId: "1:315192723360:web:733126e071a610640546c5",
    measurementId: "G-WHP68FVH93"
};

const app = initializeApp(firebaseConfig);
export const auth = getAuth(app);
const provider = new GoogleAuthProvider();

interface AuthProps {
    screen: string;
}

interface OAuthReturn {
    accessToken: string;
    success: boolean;
    email: string;
}

const Auth = (props: AuthProps) => {
    const { screen } = props;
    const switchAuthScreen = screen === 'Sign Up' ? 'I have an account' : "I don't have an acccount";
    const hrefLink = screen === 'Sign Up' ? '/signin' : '/signup';


    const capitalizeFirstLetter = (string: string): string => {
        string = string.toLowerCase();
        return string.charAt(0).toUpperCase() + string.slice(1);
    }


    const [email, setEmail] = useState<string>('');
    const [showPassword, setShowPassword] = useState<Boolean>(false);
    const [password, setPassword] = useState<string>('');
    const [redirect, setRedirect] = useState<Boolean>(false);
    const [chromeJWT, setChromeJWT] = useState<string>('');
    const [error, setError] = useState<string>('');

    useEffect(() => {
        isLoggedIn().then((isAuthenticated: Boolean) => {
            if (isAuthenticated) {
                //@ts-ignore
                const jwt: string = window.localStorage.getItem(AUTH_CONSTANTS.token);
                setChromeJWT(jwt);
                setRedirect(true);
            }
        });
    }, []);

    //@ts-ignore
    const handleSignup = async () => {
        if (email === '' || password === '') {
            setError('Please enter an email and password');
        } else {
            const apiLocation = screen === 'Sign Up' ? CONSTANTS.EMAIL_SIGNUP_AUTH : CONSTANTS.EMAIL_LOGIN_AUTH;
            const response = await fetch(`${CONSTANTS.API_ENDPOINT}${apiLocation}`, {
                method: 'POST',
                body: JSON.stringify({ email: email, password: password }),
                headers: { 'Content-Type': 'application/json' }
            });
            const data = await response.json();
            if (response.ok) {
                successfulLogin(data.token);
            } else {
                setError(data.message);

            }
        }
    };

    const successfulLogin = (token: string) => {
        window.localStorage.setItem(AUTH_CONSTANTS.token, token);
        setChromeJWT(token);
        setRedirect(true);
        setError('');
    }

    const handleGoogle = async () => {
        signInWithPopup(auth, provider)
            .then((result) => {
                // const credential = GoogleAuthProvider.credentialFromResult(result);
                fetch(`${CONSTANTS.API_ENDPOINT}${CONSTANTS.GOOGLE_AUTH}`, {
                    method: 'POST',
                    body: JSON.stringify({ idToken: result.user.uid, email: result.user.email }),
                    headers: { 'Content-Type': 'application/json' }
                })
                    //read the response and set the token
                    .then(async (response) => {
                        const data: any = await response.json();
                        if (response.ok) {
                            console.log('ok response');
                            successfulLogin(data.token);
                        } else {
                            console.log('not ok response');
                            setError(data.message);
                        }
                    })
                    .catch((error) => {
                        setError('There was an error logging in with Google');
                    });
            })
            .catch((error) => {
                setError('There was an error logging in with Google');
            });
    };

    if (redirect) {
        if (chromeJWT !== '') {
            window.postMessage({ wordsmithType: AUTH_CONSTANTS.chrome_signup, jwtToken: chromeJWT }, "*");
        }
        return <Redirect to="/dashboard" />
    }

    return (
        <div className="Auth">
            <div className="auth-container">
                <div className="auth-topContent">
                    <h1>{screen}</h1>
                    <a href={hrefLink}>{switchAuthScreen}</a>
                </div>
                <input
                    type="email"
                    id="email"
                    placeholder='Email'
                    className="auth-input"
                    value={email}
                    onClick={() => setShowPassword(true)}
                    onChange={(event: any) => setEmail(event.target.value)}
                />
                {showPassword &&
                    <input
                        type="password"
                        id="password"
                        placeholder='Pasword'
                        className="auth-input"
                        value={password}
                        onChange={event => setPassword(event.target.value)}
                    />
                }

                {error !== '' && <p className='auth-error'>{error}</p>}

                <button onClick={handleSignup} className='button1' style={{ width: "100%", marginTop: "15px", fontWeight: "400", fontSize: "14px", height: '48px' }}>{capitalizeFirstLetter(screen)}</button>

                <button className="login-with-google-btn" onClick={handleGoogle}>
                    {capitalizeFirstLetter(screen)} with Google
                </button>
                <p className='auth-legalAgreement'>
                    This site is protected by reCAPTCHA and the Google <span><a href='https://policies.google.com/privacy' target="_blank">Privacy Policy</a></span> and <span><a href='https://policies.google.com/privacy' target="_blank">Terms of Service apply</a></span>.
                </p>
            </div>
            <div className="custom-shape-divider-bottom-1674142871">
                <svg id='authSvg' data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1200 120" preserveAspectRatio="none">
                    <path d="M0,0V46.29c47.79,22.2,103.59,32.17,158,28,70.36-5.37,136.33-33.31,206.8-37.5C438.64,32.43,512.34,53.67,583,72.05c69.27,18,138.3,24.88,209.4,13.08,36.15-6,69.85-17.84,104.45-29.34C989.49,25,1113-14.29,1200,52.47V0Z" opacity=".25" className="shape-fill"></path>
                    <path d="M0,0V15.81C13,36.92,27.64,56.86,47.69,72.05,99.41,111.27,165,111,224.58,91.58c31.15-10.15,60.09-26.07,89.67-39.8,40.92-19,84.73-46,130.83-49.67,36.26-2.85,70.9,9.42,98.6,31.56,31.77,25.39,62.32,62,103.63,73,40.44,10.79,81.35-6.69,119.13-24.28s75.16-39,116.92-43.05c59.73-5.85,113.28,22.88,168.9,38.84,30.2,8.66,59,6.17,87.09-7.5,22.43-10.89,48-26.93,60.65-49.24V0Z" opacity=".5" className="shape-fill"></path>
                    <path d="M0,0V5.63C149.93,59,314.09,71.32,475.83,42.57c43-7.64,84.23-20.12,127.61-26.46,59-8.63,112.48,12.24,165.56,35.4C827.93,77.22,886,95.24,951.2,90c86.53-7,172.46-45.71,248.8-84.81V0Z" className="shape-fill"></path>
                </svg>
            </div>
        </div>
    );
}

//{error && <p>{error.message}</p>}
export default Auth;